<template>
<div class="qmsj-list">
  <div class="btn-list">
    <mt-button size="large" type="primary" plain v-for="(v,k) in btnList" :key="k" @click="go(v.qmsj_project_id)">{{v.project_title}}</mt-button>
  </div>
  <div class="btn-ques" @click="goQuestionList"><mt-button size="large" type="primary">查看问卷列表</mt-button></div>  
</div>
</template>

<script>
import {
  getAllowProjectList
} from "@/api/http"
export default {
  data() {
    return {
      btnList: [],
      imgList: [],
      imgUrl: ''
    }
  },
  created() {
    this.getProjectList()
  },
  methods: {
    getProjectList() {
      getAllowProjectList()
        .then((res) => {
          // console.log(res.allow_qmsj_project_list)
          this.btnList = res.allow_qmsj_project_list
        })
    },
    go(id) {
      this.$router.push({
        name: 'ask',
        params: {
          id: id
        }
      })
    },
    goQuestionList(){
      this.$router.push({name:'askList'})
    }
  }
}
</script>

<style lang="less" scoped>
.qmsj-list{
  position: relative;
  .btn-ques{
    position: fixed;
    bottom: 0;
    width: 375px;
    font-size: 0;
    height: 56px;

  }
}
.btn-list {
  padding: 0 15px 80px 15px;
}

button {
  margin: 15px 0;
}
</style>



